<template>
  <div class="wrapper-covid">
    <section>
      <div class="content">
        <transition name="bounce" mode="out-in">
          <div v-if="questionIndex < 0" class="intro">
            <div class="logo"></div>
            <h1>Coronavirus test</h1>
            <p>Check yourself can you be a carrier of coronavirus</p>
            <div class="button-ready">
              <button class="btn" @click="questionIndex = 0">Play Now</button>
            </div>
          </div>
        </transition>
        <div
          v-show="questionIndex < quiz.questions.length && questionIndex > -1"
          class="game"
        >
          <div v-for="(question, index) in quiz.questions" :key="index">
            <transition name="no-mode-translate-fade">
              <div
                v-show="index === questionIndex"
                :key="index"
                class="subject"
              >
                <h2>{{ question.text }}</h2>
                <div class="list-answer">
                  <span
                    v-for="(response, ans) in question.responses"
                    :key="index + '-' + ans"
                  >
                    <label class="checkbox-label">
                      <input type="radio" />
                      <span
                        class="checkbox-title"
                        :class="{
                          error: userAnswers[index] == ans && !response.correct,
                          success:
                            userAnswers[index] !== undefined && response.correct
                        }"
                        @click="sendAnswer(index, ans)"
                      >
                        {{ response.text }}
                        <img v-if="response.src" :src="response.src" />
                        <p
                          v-if="
                            userAnswers[index] !== undefined && response.correct
                          "
                          class="info-answer"
                          v-html="response.answer || 'The answer is correct'"
                        ></p>
                        <p
                          v-if="userAnswers[index] == ans && !response.correct"
                          class="info-error"
                          v-html="response.error || 'The answer is incorrect'"
                        ></p>
                      </span>
                    </label>
                  </span>
                </div>
                <div class="button-ready">
                  <button
                    class="btn next"
                    :disabled="userAnswers[index] === undefined"
                    @click="next"
                  >
                    next
                  </button>
                </div>
              </div>
            </transition>
          </div>
        </div>
        <transition name="bounce" mode="out-in">
          <div v-show="questionIndex === quiz.questions.length" class="result">
            <h2>
              Finished
            </h2>
            <p>Total score: {{ score() }} / {{ quiz.questions.length }}</p>
            <div class="button-ready">
              <button class="btn" @click="restart()">Restart</button>
            </div>
          </div>
        </transition>
      </div>
    </section>
    <div class="bg"></div>
  </div>
</template>

<script>
const quiz = {
  title: "Coronavirus test",
  description: "Check yourself can you be a carrier of coronavirus",
  questions: [
    {
      text: "How long can symptoms of a new coronavirus infection develop?",
      responses: [
        { text: "2 days" },
        {
          text: "14 days",
          correct: true,
          answer:
            "Symptoms may occur within 14 days after contact with an infectious patient."
        },
        { text: "2 months" },
        { text: "1 month" }
      ]
    },
    {
      text: "In which country was the new COVID-19 virus first discovered?",
      responses: [
        {
          text: "Italy"
        },
        { text: "Germany" },
        {
          text: "China",
          correct: true,
          answer:
            "The new virus was discovered in late December 2019 in the city of Wuhan, Hubei province of Central China."
        },
        { text: "USA" }
      ]
    },
    {
      text: "How is the virus transmitted?",
      responses: [
        {
          text: "By air and by handshakes",
          correct: true,
          answer:
            "True, you can get infected with coronavirus by airborne droplets and through food and household items contaminated with the virus."
        },
        { text: "From Pets" },
        { text: "Through blood transfusions" },
        { text: "This is a hereditary disease" }
      ]
    },
    {
      text: "Immunity to COVID-19 is...",
      responses: [
        { text: "Lifelong" },
        { text: "5-6 years old" },
        { text: "20 years old" },
        {
          text: "You can get sick again",
          correct: true,
          answer: "The immune system is unstable, you can get sick again"
        }
      ]
    },
    {
      text: "Can I get infected in a warm country?",
      responses: [
        { text: "No, the virus doesn 't survive hot temperatures" },
        { text: "Yes, but where it is very cold, there is no virus" },
        { text: "You can only get infected in China and Italy" },
        {
          text: "Yes, you can get it in any country",
          correct: true,
          answer:
            "The virus is transmitted from person to person regardless of air temperature"
        }
      ]
    },
    {
      text: "What about the vaccine?",
      responses: [
        { text: "There is no cure and it cannot be created" },
        {
          text:
            "Scientists have already developed an antibiotic, but it is not available to ordinary people"
        },
        {
          text:
            "The drug has been around for a long time , but it's too expensive"
        },
        {
          text:
            "The vaccine is not ready yet, but doctors are already determining the strain",
          correct: true,
          answer:
            "Scientists in China have bred two main strains of Covid-19 disease that circulate in the human body and cause infections"
        }
      ]
    }
  ]
};

export default {
  data() {
    return {
      quiz: quiz,
      questionIndex: -1,
      userAnswers: []
    };
  },
  methods: {
    next: function() {
      this.questionIndex++;
    },
    prev: function() {
      this.questionIndex--;
    },
    score: function() {
      return this.userAnswers.filter((val, index) => {
        return this.quiz.questions[index].responses[val].correct;
      }).length;
    },
    restart() {
      this.questionIndex = 0;
      this.userAnswers = [];
    },
    sendAnswer(index, ans) {
      if (this.userAnswers[index] === undefined) {
        this.userAnswers.push(ans);
      }
    }
  }
};
</script>

<style>
.no-mode-translate-fade-enter-active {
  transition: all 1s;
  transition-delay: 1s;
}
.no-mode-translate-fade-leave-active {
  transition: all 1s;
}
.no-mode-translate-fade-enter,
.no-mode-translate-fade-leave-active {
  opacity: 0;
}
.no-mode-translate-fade-enter {
  transform: translateX(60px);
}
.no-mode-translate-fade-leave-active {
  transform: translateX(-60px);
}

.bounce-enter-active {
  animation: bounce-in 0.8s;
}
.bounce-leave-active {
  animation: bounce-in 0.8s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
</style>
